import React from 'react'
import { NavLink } from "react-router-dom";
import web from "../images/intro2.png";

function Common() {
    
    const pass = {
        color : "red"
    
    }
    return (
        <>
        <section id="header" className="d-flex align-items-center">
         <div className="container-fluid nav_bg">
             <div className="row">
                 <div className="col-10 mx-auto">
                     <div className="row">
                     <div className="col-md-6 pt-5 pt-lg-0 order-2 order-lg-1 d-flex justify-content-center flex-column">
                         <h1>Grow your  bussines With <strong style={pass}> ALSAFE </strong></h1>
                         <h3 className="my-3">
                             We Are Group of Some Passinate Developers
                         </h3>

                          <div className="mt-3">
                              <NavLink to="/Services" className="btn btn-danger get-started">
                                  Get Started
                              </NavLink>
                          </div>
                     </div>

                     <div className="col-lg-6 order-1 order-lg-2 header-img">
                         <img src={web} className="img-fluid animated" alt="homeimg" />
                     </div>
                     </div>
             </div>
           </div>
         </div>
        </section>
    </>
    )
}

export default Common
